/*================================================
Default CSS
=================================================*/
body {
  padding: 0;
  margin: 0;
  font-size: 16px;
  font-family: 'Nunito', sans-serif;
}

img {
  max-width: 100%;
  height: auto;
}

p {
  color: #606060;
  margin-bottom: 15px;
  line-height: 1.6;
  font-size: 16px;
}

p:last-child {
  margin-bottom: 0;
}

:focus {
  outline: 0 !important;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #221638;
  font-family: 'Nunito', sans-serif;
}

.container {
  max-width: 1230px;
}

.ptb-100 {
  padding-top: 100px;
  padding-bottom: 100px;
}

.pt-100 {
  padding-top: 100px;
}

.pt-70 {
  padding-top: 70px;
}

.pb-70 {
  padding-bottom: 70px;
}

a {
  color: #221638;
  transition: 0.5s;
  text-decoration: none;
}

a:hover {
  color: #fe4a55;
  text-decoration: none;
}

.d-table {
  width: 100%;
  height: 100%;
}

.d-table-cell {
  vertical-align: middle;
}

.vertical-align-top td,
.vertical-align-top th {
  vertical-align: middle;
}

.text-center {
  text-align: center;
}

/* default-btn */
.default-btn {
  border: none;
  position: relative;
  display: inline-block;
  text-align: center;
  overflow: hidden;
  z-index: 1;
  color: #ffffff;
  background-color: #fe4a55;
  transition: 0.5s;
  border-radius: 5px;
  font-weight: 700;
  font-size: 16px;
  padding-left: 55px;
  padding-right: 30px;
  padding-top: 11px;
  padding-bottom: 11px;
}

.default-btn span {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  border-radius: 50%;
  background-color: #221638;
  transition: width 0.5s ease-in-out, height 0.5s ease-in-out;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  z-index: -1;
  border-radius: 5px;
}

.default-btn i {
  position: absolute;
  left: 30px;
  top: 10px;
}

.default-btn:hover {
  color: #ffffff;
}

.default-btn:hover span {
  width: 225%;
  height: 562.5px;
}

/* form-control */
.form-control {
  height: 50px;
  color: #221638;
  box-shadow: unset !important;
  border: 1px solid #f5f5f5;
  background-color: #f5f5f5;
  transition: 0.5s;
  border-radius: 3px;
  padding: 1px 0 0 15px;
  font-size: 16px;
  font-weight: 400;
}

.form-control::-webkit-input-placeholder {
  color: #7e7e7e;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.form-control:-ms-input-placeholder {
  color: #7e7e7e;
  -ms-transition: 0.5s;
  transition: 0.5s;
}

.form-control::placeholder {
  color: #7e7e7e;
  transition: 0.5s;
}

.form-control:focus {
  border-color: #fe4a55;
