.login-form form .form-group {
  margin-bottom: 25px;
}

.login-form form .form-group label {
  display: block;
  margin-bottom: 8px;
  color: #221638;
  font-weight: 700;
}

.login-form form .remember-me-wrap {
  margin-bottom: 0;
}

.login-form form .remember-me-wrap [type='checkbox']:checked,
.login-form form .remember-me-wrap [type='checkbox']:not(:checked) {
  display: none;
}

.login-form form .remember-me-wrap [type='checkbox']:checked + label,
.login-form form .remember-me-wrap [type='checkbox']:not(:checked) + label {
  position: relative;
  padding-left: 28px;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;
  margin-bottom: 0;
  color: #606060;
  font-weight: 600;
}

.login-form form .remember-me-wrap [type='checkbox']:checked + label:before,
.login-form form .remember-me-wrap [type='checkbox']:not(:checked) + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 19px;
  height: 19px;
  transition: all 0.2s ease;
  border: 1px solid #f5f5f5;
  border-radius: 3px;
  background: #f5f5f5;
}

.login-form form .remember-me-wrap [type='checkbox']:checked + label:after,
.login-form form .remember-me-wrap [type='checkbox']:not(:checked) + label:after {
  content: '';
  width: 8px;
  height: 8px;
  background: #fe4a55;
  position: absolute;
  top: 5.5px;
  left: 6px;
  transition: all 0.2s ease;
}

.login-form form .remember-me-wrap [type='checkbox']:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0);
}

.login-form form .remember-me-wrap [type='checkbox']:checked + label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

.login-form form .remember-me-wrap [type='checkbox']:hover + label:before {
  border-color: #fe4a55;
}

.login-form form .remember-me-wrap [type='checkbox']:checked + label:before {
