  width: 100%;
  height: 1px;
  position: absolute;
  left: 0;
  bottom: 0;
  content: '';
  transition: 0.5s;
  background-color: #eeeeee;
}

.login-form form .lost-your-password-wrap a::after {
  width: 0;
  height: 1px;
  position: absolute;
  left: 0;
  transition: 0.5s;
  bottom: 0;
  content: '';
  background-color: #fe4a55;
}

